import React from 'react'
import { Route, Redirect } from 'react-router-dom'
import { useSelector } from 'react-redux'
const withLayout = (WrapComponent) => {
    // res l ly ra nhng th cn li t props (destructuring)
    //routeProps l thuc tnh props ca route
    return ({ component: Component, isPrivate, ...rest }) => {
        const currentUser = useSelector((state) => state.authReducer.currentUser);
        const content = (
            < Route
                {...rest}
                render={(routeProps) => (
                    <WrapComponent>
                        <Component {...routeProps} />
                    </WrapComponent>
                )}
            />
        )

        if (isPrivate) {
            //kim tra xem ang nhp cha mi cho vo trang mua v
            if (currentUser) {
                return content
            } else {
                alert("Please Login!")
                return <Redirect to='/' />
            }
        } else {
            //cc route bnh thng (isPrivate === false)
            return content
        }
    }

}
export default withLayout;