import "./popup.scss";
import React, { Component } from "react";

export default class Popup extends Component {
  render() {
    return (
      <div
        className="modal fade "
        id="staticBackdrop"
        data-backdrop="static"
        data-keyboard="false"
        tabIndex={-1}
        aria-labelledby="staticBackdropLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog modal-xl">
          <div className="modal-content">
            <div className="modal-header">
              <h3 className="modal-title text-center w-100" id="staticBackdropLabel">
                Membership Terms &amp; Conditions
              </h3>
              <button
                type="button"
                className="close"
                data-dismiss="modal"
                aria-label="Close"
                style={{backgroundColor:'#e00d7a'}}
              >
              </button>
            </div>
            <div className="modal-body text-left">
              <p>
                <strong>Th CFriend: </strong>
              </p>
              <p>
                -&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; c cp ln u khi
                mua 2 v xem phim bt k ti Cinestar.
              </p>
              <p>
                -&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; c tch ly im
                theo gi tr mua hng ha dch v nh sau
              </p>
              <p>
                <img className="img-fluid w-100" src="http://cinestar.com.vn/pictures/webimages/3Khuyenmai/member/point.jpg" />
              </p>
              <p>
                -&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; c gim 10% trc tip
                trn gi tr ha n bp nc khi mua ti quy.
              </p>
              <p>
                -&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; c tng 1 v xem phim 2D
                vo tun sinh nht (tnh t Th Hai n Ch Nht) vi s im
                tch ly ti thiu 500 im.
              </p>
              <p>
                -&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; c tham gia cc chng
                trnh dnh cho thnh vin.
              </p>
            </div>
            <div className="modal-footer">
              <button
                type="button"
                className="btn btn-danger"
                data-dismiss="modal"
              >
                NG
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
